.main{
    /* background:black; */
}
#calculator{
    /* padding: 134px 323px 133px 308px; */
    background: black;
    color:white;
    width: 300px;
    height: 370px;
    align-self: center;
    margin: 130px auto;
    justify-content: flex-end;
}
#buttons{
    display:flex;
    flex-flow:column;
    width: 300px;
    margin-top: -10px;
}
#display{
    height: 80px;
    font-size: 30px;
    color:#fff;
    background: #000;
}
#display p{
    margin-top: 16px;
    margin-left: -6px;
}
#buttons  button{
    width: 75px;
    height: 60px;
    border: none;
    background: black;
    outline: 1px solid #fff;
    color:#fff;
    cursor: pointer;
}
#buttons  button.bigWidth{
    width: 150px;
    height: 60px;
}
#buttons  button#equals{
    height:120px;
}
#buttons  button[name*="AC"]{
    background: darkred;
}
#buttons  button[name*="="]{
    background:darkslategray;;
}
#buttons  button[name*="+"],#buttons  button[name*="-"],#buttons  button[name*="*"],#buttons  button[name*="/"]{
    background: gray;
}
.forFlex{
    display:flex;
}
.forFlex div{
    width: 225px;
}
